import Vue from "vue";
import Vuex from "vuex";
import tunnukset from "./vuex/UserDtActions.js"
import mittauspisteet from "./vuex/MeasurementPlaceActions.js"

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    graphData: {},
    graphOptions: {},
  },
  modules: {
    tunnukset,
    mittauspisteet
  },
  mutations: {
    changeGraphData(state, data) {
      state.graphData = data;
    },
    changeGraphOptions(state, options) {
      state.graphOptions = options;
    }
  },
  getters: {
    graphData: state => state.graphData,
    graphOptions: state => state.graphOptions,
  },
  actions: {}
});