<template>
  <div>
    <label>{{label}}</label>
    <b-form-select :value="value" @input="emit">
      <option v-for="option in options" :key="option">{{option}}</option>
    </b-form-select>
  </div>
</template>

<script>
export default {
  name: "selectList",
  props: ["options", "name", "label", "value"],
  methods: {
    emit(value) {
      this.$emit("input", value);
    }
  }
};
</script>

