<template>
  <div>
    <v-toolbar flat color="white" class="toolbar">
      <v-layout row wrap>
        <v-spacer></v-spacer>
        <v-flex xs12 sm3 md3 lg2 class="searchmodule">
          <v-text-field
          v-model="search"
          append-icon="search"
          label="Search"
          single-line
          hide-details
        ></v-text-field>  
        <b-form-select v-model="searchcompany" > 
            <option :value="''" selected>Yritys</option>
            <option v-for="item in uniqueNames" :key="item.yritys" >{{ item }}</option>
        </b-form-select>    
        </v-flex>
        <v-dialog v-model="dialog" max-width="500px" persistent>
          <v-btn slot="activator" dark class="mb-2" color="#575756">Lisää käyttäjä</v-btn>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container grid-list-lg>
                <v-layout wrap>
                  <v-flex xs12 sm6 md4>
                    <v-text-field v-model="editedItem.tunnus" label="Tunnus"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field v-model="editedItem.etunimi" label="Etunimi"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field v-model="editedItem.sukunimi" label="Sukunimi"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field v-model="editedItem.sähköposti" label="Sähköposti"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field v-model="editedItem.puhelinnro" label="Puhelinnro"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field v-model="editedItem.yritys" label="Yritys"></v-text-field>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn id="peruutabutton" color="#005479" flat @click="close">Peruuta</v-btn>
              <v-btn id="tallennabutton" color="#005479" flat @click="save">Tallenna</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
       </v-layout>


        <v-dialog v-model="dialog2" max-width="500px" persistent>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container grid-list-lg>
                <v-layout wrap>
                  <v-flex xs12 sm6 md4>
                    <v-text-field disabled v-model="editedItem.tunnus" label="Tunnus"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field disabled v-model="editedItem.etunimi" label="Etunimi"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field disabled v-model="editedItem.sukunimi" label="Sukunimi"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field disabled v-model="editedItem.sähköposti" label="Sähköposti"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field disabled v-model="editedItem.puhelinnro" label="Puhelinnro"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field disabled v-model="editedItem.yritys" label="Yritys"></v-text-field>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn id="peruutabutton" color="#005479" flat @click="close">Peruuta</v-btn>
              <v-btn id="muokkaabutton" color="#005479" flat @click="editItem(rownumber)">Muokkaa</v-btn>
              <v-btn id="poistabutton" color="#005479" flat @click="deleteItem(rownumber)">Poista</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
    </v-toolbar>
    <v-layout>
      <v-flex class="hidden-xs-only">
    <v-data-table
      :headers="headers"
      :items="tableFilter"
      :search="search"
      class="elevation-1 pt-3"
      :pagination.sync="pagination"
    >
      <template slot="items" slot-scope="props">
        <td>{{ props.item.tunnus }}</td>
        <td>{{ props.item.etunimi }}</td>
        <td>{{ props.item.sukunimi }}</td>
        <td>{{ props.item.sähköposti }}</td>
        <td>{{ props.item.puhelinnro }}</td>
        <td>{{ props.item.yritys }}</td>
        <td class="justify-center layout px-0">
          <v-icon small class="mr-2" @click="editItem(props.item)">edit</v-icon>
          <v-icon small @click="deleteItem(props.item)">delete</v-icon>
        </td>
      </template>
    <template slot="no-data">
      <v-alert :value="true" color="error" icon="warning">
        Sorry, nothing to display here :(
      </v-alert>
    </template>
    </v-data-table>
      </v-flex>
    </v-layout>
    <v-layout>
      <v-flex class="hidden-sm-and-up">
    <v-data-table
      :headers="headers2"
      :items="tableFilter"
      :search="search" 
      class="elevation-1 pt-5"
      :pagination.sync="pagination"
    >
      <template slot="items" slot-scope="props">
        <td>{{ props.item.tunnus }}</td>
        <td>{{ props.item.etunimi }}</td>
        <td>{{ props.item.sukunimi }}</td>
        <td hidden>{{ props.item.sähköposti }}</td>
        <td hidden>{{ props.item.puhelinnro }}</td>
        <td hidden>{{ props.item.yritys }}</td>
        <td class="justify-center layout px-0">
        <v-menu offset-y>
        <v-btn slot="activator" fab dark color="#575756" small class="menubutton">
          <v-icon>list</v-icon>
        </v-btn>
        <v-list>
          <v-list-tile @click="watchitem(props.item)">Katsele</v-list-tile>
          <v-list-tile @click="editItem(props.item)">Muokkaa</v-list-tile>
          <v-list-tile @click="deleteItem(props.item)">Poista</v-list-tile>
        </v-list>
      </v-menu>
        </td>
      </template>
    <template slot="no-data">
      <v-alert :value="true" color="error" icon="warning">
        Sorry, nothing to display here :(
      </v-alert>
    </template>
    </v-data-table>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import 'vuetify/dist/vuetify.min.css'
import { mapGetters, mapMutations } from 'vuex'

  export default {
    data: () => ({
        rownumber: '',
        dialog: false,
        dialog2: false,
        search: '',
        searchcompany: '',
        pagination: {
          rowsPerPage: 10
        },
         items: [
        { title: 'Katsele' },
        { title: 'Muokkaa' },
        { title: 'Poista' }
      ],
        headers: [
          {
            text: 'Tunnus',
            align: 'left',
            sortable: false,
            value: 'tunnus'
          },
          { text: 'Etunimi', value: 'etunimi' },
          { text: 'Sukunimi', value: 'sukunimi' },
          { text: 'Sähköposti', value: 'sähköposti' },
          { text: 'Puhelinnro', value: 'puhelinnro' },
          { text: 'Yritys', value: 'yritys' },
          { text: '', value: '' , sortable: false}
        ],
        headers2: [
          {
            text: 'Tunnus',
            align: 'left',
            sortable: false,
            value: 'tunnus'
          },
          { text: 'Etunimi', value: 'etunimi'},
          { text: 'Sukunimi', value: 'sukunimi' },
          { text: 'Sähköposti', value: 'sähköposti', class:"header_hidden" },
          { text: 'Puhelinnro', value: 'puhelinnro', class:"header_hidden"  },
          { text: 'Yritys', value: 'yritys', class:"header_hidden"  },
          { text: '', value: '' , sortable: false}
        ],
      editedIndex: -1,
      editedItem: {
        tunnus: '',
        etunimi: '',
        sukunimi: '',
        sähköposti: '',
        puhelinnro: '',
        yritys: '',
      },
      defaultItem: {
        tunnus: '',
        etunimi: '',
        sukunimi: '',
        sähköposti: '',
        puhelinnro: '',
        yritys: '',
      }
    }),
    computed:{
      tunnukset() {
        return this.$store.getters.getUsers
      },
      formTitle () {
        return this.editedIndex === -1 ? 'Lisää käyttäjä' : 'Muokkaa käyttäjää'
      },
      tableFilter() {
          return this.tunnukset.filter((item) => 
             item.yritys === this.searchcompany || "" === this.searchcompany);
      },
      uniqueNames: function() {
        let filtered_array = [];
        for(let i =0; i < this.tunnukset.length; i++) {
          if(filtered_array.indexOf(this.tunnukset[i].yritys) === -1) {
            filtered_array.push(this.tunnukset[i].yritys)
          }
        }
        return filtered_array;
      }
    },
    watch: {
      dialog (val) {
        this.hideicons();
        val || this.close()
      }
    },
    methods: {
      ...mapMutations([
      'DELETE_ITEM',
      'SAVE'
    ]),
      editItem (item){
        this.editedIndex = this.tunnukset.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.hideicons();
        this.dialog = true
      },
      deleteItem (item){
        const index = this.tunnukset.indexOf(item)
        this.DELETE_ITEM(index);
        this.revealicons();
        this.dialog2 = false
      },
      save (){
        if (this.editedIndex > -1) {
        Object.assign(this.tunnukset[this.editedIndex], this.editedItem)
        } else {
          this.SAVE(this.editedItem);
        }
        this.close()
      },
      hideicons (){
        let x = document.getElementsByClassName('menubutton');
        for (let i = 0; i < x.length; i++) {
            x[i].style.display = 'none';}
        x = document.getElementsByClassName('v-datatable__actions__range-controls');
        for (let i = 0; i < x.length; i++) {
            x[i].style.display = 'none';}
      },
      revealicons (){
        let x = document.getElementsByClassName('menubutton');
        for (let i = 0; i < x.length; i++) {
            x[i].style.display = 'block';}
        x = document.getElementsByClassName('v-datatable__actions__range-controls');
        for (let i = 0; i < x.length; i++) {
            x[i].style.display = 'block';}
      },
      watchitem (item) {
        this.rownumber = item;
        this.hideicons();
        this.editedIndex = this.tunnukset.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog2 = true
      },   
      close () {
        this.revealicons();
        this.dialog = false
        this.dialog2 = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 20)
      },
    }
  }
</script>

<style>
.v-dialog {
  position: absolute;
  top: 10px;
  min-height: 340px !important;
}
.v-datatable__actions__select{
  display: none
}
.v-alert{
  color: #575756;
}
.header_hidden{
  display: none;
}
@media screen and (max-width: 600px){
  .searchmodule{
    padding-top: 25px;
  }
}
.toolbar{
  height: 80px
}
</style>
