<template>
  <div>
    <b-row>
      <b-col sm="3">
        <label>{{label}}</label>
      </b-col>
      <b-col>
        <b-form-input type="date" :name="name" :value="value" @input="emit"/>
      </b-col>
    </b-row>
  </div>
</template>

<script>
export default {
  name: "dateInput",
  props: ["name", "value", "label"],
  data() {
    return {};
  },
  methods: {
    emit(value) {
      this.$emit("input", value);
    }
  }
};
</script>

