<template>
  <div>
    <v-toolbar flat color="white" class="toolbar">
      <v-dialog v-model="dialog" max-width="500px" persistent>
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>

          <v-card-text>
            <v-container grid-list-lg>
              <v-layout wrap>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.nimi" label="Nimi"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.osoite" label="Osoite"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.koordinaatit" label="Koordinaatit"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.pyynnönlähettäjä" label="Pyynnönlähettäjä"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.yritys" label="Yritys"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="#005479" flat @click="close">Peruuta</v-btn>
            <v-btn id="tallennabutton" color="#005479" flat>Tallenna</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-dialog v-model="dialog2" max-width="500px" persistent>
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>

          <v-card-text>
            <v-container grid-list-lg>
              <v-layout wrap>
                <v-flex xs12 sm6 md4>
                  <v-text-field disabled v-model="editedItem.nimi" label="Nimi"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field disabled v-model="editedItem.osoite" label="Osoite"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field disabled v-model="editedItem.koordinaatit" label="Koordinaatit"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field disabled v-model="editedItem.pyynnönlähettäjä" label="Pyynnönlähettäjä"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field disabled v-model="editedItem.yritys" label="Yritys"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="#005479" flat @click="close">Peruuta</v-btn>
            <v-btn id="muokkaabutton" color="#005479" flat @click="editItem(rownumber)">Muokkaa</v-btn>
            <v-btn color="#005479" flat @click="deleteItem(rownumber)">Poista</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="dialog3" max-width="500px" persistent>
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>

          <v-card-text>
            <v-container grid-list-lg>
              <v-layout wrap>
                <v-flex xs12 sm6 md4>
                  <v-text-field disabled v-model="editedItem.nimi" label="Nimi"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field disabled v-model="editedItem.osoite" label="Osoite"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field disabled v-model="editedItem.koordinaatit" label="Koordinaatit"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field disabled v-model="editedItem.pyynnönlähettäjä" label="Pyynnönlähettäjä"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <v-text-field disabled v-model="editedItem.yritys" label="Yritys"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="#005479" flat @click="close">Peruuta</v-btn>
            <v-btn id="muokkaabutton" color="#005479" flat @click="editItem3(rownumber)">Muokkaa</v-btn>
            <v-btn color="#005479" flat @click="deleteItem2(rownumber)">Poista</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <div v-if="tableFilter.length > 0">
      <v-layout row wrap>
        <v-spacer></v-spacer>
        <v-flex xs12 sm3 md3 lg2 class="searchmodule pr-3 pl-3">
          <div id="search">
            <v-text-field
              v-model="search"
              append-icon="search"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </div>
          <b-form-select v-model="searchcompany">
            <option :value="''" selected>Yritys</option>
            <option v-for="item in uniqueNames" :key="item.yritys">{{ item }}</option>
          </b-form-select>
        </v-flex>
      </v-layout>

      <v-layout>
        <v-flex class="hidden-xs-only">
          <v-data-table
            v-model="selected"
            :headers="headers"
            :items="tableFilter"
            :search="search"
            item-key="nimi"
            select-all
            class="elevation-1"
            :pagination.sync="pagination"
          >
            <template v-slot:items="props">
              <td>
                <v-checkbox v-model="props.selected" primary hide-details></v-checkbox>
              </td>
              <td>{{ props.item.nimi }}</td>
              <td>{{ props.item.osoite }}</td>
              <td>{{ props.item.koordinaatit }}</td>
              <td>{{ props.item.pyynnönlähettäjä }}</td>
              <td>{{ props.item.yritys }}</td>
              <td class="justify-center layout px-0">
                <v-icon small class="mr-2" @click="editItem(props.item)">edit</v-icon>
                <v-icon small @click="deleteItem(props.item)">delete</v-icon>
              </td>
            </template>
            <template slot="no-data">
              <v-alert :value="true" color="error" icon="warning">Sorry, nothing to display here :(</v-alert>
            </template>
          </v-data-table>
        </v-flex>
      </v-layout>
      <v-layout>
        <v-flex class="hidden-sm-and-up">
          <v-data-table
            v-model="selected"
            :headers="headers2"
            :items="tableFilter"
            :search="search"
            item-key="nimi"
            select-all
            class="elevation-1 pt-2"
            :pagination.sync="pagination"
          >
            <template v-slot:items="props">
              <td>
                <v-checkbox v-model="props.selected" primary hide-details class="pcheckbox"></v-checkbox>
              </td>
              <td hidden>{{ props.item.nimi }}</td>
              <td>{{ props.item.osoite }}</td>
              <td hidden>{{ props.item.koordinaatit }}</td>
              <td hidden>{{ props.item.pyynnönlähettäjä }}</td>
              <td>{{ props.item.yritys }}</td>
              <td class="justify-center layout px-0">
                <v-menu offset-y>
                  <v-btn slot="activator" fab dark color="#575756" small class="menubutton">
                    <v-icon>list</v-icon>
                  </v-btn>
                  <v-list>
                    <v-list-tile @click="watchitem(props.item)">Katsele</v-list-tile>
                    <v-list-tile @click="editItem2(props.item)">Muokkaa</v-list-tile>
                    <v-list-tile @click="deleteItem(props.item)">Poista</v-list-tile>
                  </v-list>
                </v-menu>
              </td>
            </template>
            <template slot="no-data">
              <v-alert :value="true" color="error" icon="warning">Sorry, nothing to display here :(</v-alert>
            </template>
          </v-data-table>
        </v-flex>
      </v-layout>

      <div class="text-xs-right pt-2" id="delete/acceptdiv">
        <v-btn dark class="mb-2" color="#575756" @click="deletechosen">Poista valitut</v-btn>
        <v-btn dark class="mb-2" color="#575756" @click="acceptchosen">Hyväksy valitut</v-btn>
      </div>
    </div>

    <h1 class="header1">Mittauspisteet</h1>
    <div>
      <v-layout row wrap>
        <v-spacer></v-spacer>
        <v-flex xs12 sm3 md3 lg2 class="searchmodule pr-3 pl-3">
          <v-text-field
            v-model="search2"
            append-icon="search"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
          <b-form-select v-model="searchcompany2">
            <option :value="''" selected>Yritys</option>
            <option v-for="item in uniqueNames2" :key="item.yritys">{{ item }}</option>
          </b-form-select>
        </v-flex>
      </v-layout>
    </div>
    <v-layout>
      <v-flex class="hidden-xs-only">
        <v-data-table
          v-model="selected2"
          :headers="headers"
          :items="tableFilter2"
          :search="search2"
          item-key="nimi"
          select-all
          class="elevation-1"
          :pagination.sync="pagination"
        >
          <template v-slot:items="props">
            <td>
              <v-checkbox v-model="props.selected" primary hide-details></v-checkbox>
            </td>
            <td>{{ props.item.nimi }}</td>
            <td>{{ props.item.osoite }}</td>
            <td>{{ props.item.koordinaatit }}</td>
            <td>{{ props.item.pyynnönlähettäjä }}</td>
            <td>{{ props.item.yritys }}</td>
            <td class="justify-center layout px-0">
              <v-icon small class="mr-2" @click="editItem3(props.item)">edit</v-icon>
              <v-icon small @click="deleteItem2(props.item)">delete</v-icon>
            </td>
          </template>
          <template slot="no-data">
            <v-alert :value="true" color="error" icon="warning">Sorry, nothing to display here :(</v-alert>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>

    <v-layout>
      <v-flex class="hidden-sm-and-up">
        <v-data-table
          v-model="selected2"
          :headers="headers2"
          :items="tableFilter2"
          :search="search2"
          item-key="nimi"
          select-all
          class="elevation-1 pt-2"
          :pagination.sync="pagination"
        >
          <template v-slot:items="props">
            <td>
              <v-checkbox v-model="props.selected" primary hide-details></v-checkbox>
            </td>
            <td hidden>{{ props.item.nimi }}</td>
            <td>{{ props.item.osoite }}</td>
            <td hidden>{{ props.item.koordinaatit }}</td>
            <td hidden>{{ props.item.pyynnönlähettäjä }}</td>
            <td>{{ props.item.yritys }}</td>
            <td class="justify-center layout px-0">
              <v-menu offset-y>
                <v-btn slot="activator" fab dark color="#575756" small class="menubutton2">
                  <v-icon>list</v-icon>
                </v-btn>
                <v-list>
                  <v-list-tile @click="watchitem2(props.item)">Katsele</v-list-tile>
                  <v-list-tile @click="editItem4(props.item)">Muokkaa</v-list-tile>
                  <v-list-tile @click="deleteItem2(props.item)">Poista</v-list-tile>
                </v-list>
              </v-menu>
            </td>
          </template>
          <template slot="no-data">
            <v-alert :value="true" color="error" icon="warning">Sorry, nothing to display here :(</v-alert>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
    <div class="text-xs-right pt-2">
      <v-btn dark class="mb-2" color="#575756" @click="deletechosen2">Poista valitut</v-btn>
    </div>
  </div>
</template>

<script>
import "vuetify/dist/vuetify.min.css";
import { mapMutations } from "vuex";

export default {

  data: () => ({
    rownumber: "",
    search: "",
    searchcompany: "",
    search2: "",
    searchcompany2: "",
    dialog: false,
    dialog2: false,
    dialog3: false,
    selected: [],
    selected2: [],
    pagination: {
      rowsPerPage: 10
    },
    items: [{ title: "Katsele" }, { title: "Muokkaa" }, { title: "Poista" }],
    headers: [
      { text: "Nimi", align: "left", value: "nimi" },
      { text: "Osoite", value: "osoite" },
      { text: "Koordinaatit", value: "koordinaatit" },
      { text: "Pyynnönlähettäjä", value: "pyynnönlähettäjä" },
      { text: "Yritys", value: "yritys" },
      { text: "", value: "", sortable: false }
    ],
    headers2: [
      { text: "Nimi", align: "left", value: "nimi", class: "header_hidden" },
      { text: "Osoite", value: "osoite" },
      { text: "Koordinaatit", value: "koordinaatit", class: "header_hidden" },
      {
        text: "Pyynnönlähettäjä",
        value: "pyynnönlähettäjä",
        class: "header_hidden"
      },
      { text: "Yritys", value: "yritys" },
      { text: "", value: "", sortable: false }
    ],
    editedIndex: -1,
    editedItem: {
      nimi: "",
      osoite: "",
      koordinaatit: "",
      pyynnönlähettäjä: "",
      yritys: ""
    },
    defaultItem: {
      nimi: "",
      osoite: "",
      koordinaatit: "",
      pyynnönlähettäjä: "",
      yritys: ""
    }
  }),
  computed: {
    mittauspistehakemukset() {
      return this.$store.getters.getApplications;
    },
    mittauspaikat() {
      return this.$store.getters.getPlaces;
    },
    formTitle() {
      return this.editedIndex === -1 ? "Lisää käyttäjä" : "Muokkaa käyttäjää";
    },
    tableFilter() {
      return this.mittauspistehakemukset.filter(
        item => item.yritys === this.searchcompany || "" === this.searchcompany
      );
    },
    uniqueNames: function() {
      let filtered_array = [];
      for (let i = 0; i < this.mittauspistehakemukset.length; i++) {
        if (
          filtered_array.indexOf(this.mittauspistehakemukset[i].yritys) === -1
        ) {
          filtered_array.push(this.mittauspistehakemukset[i].yritys);
        }
      }
      return filtered_array;
    },
    tableFilter2() {
      return this.mittauspaikat.filter(
        item =>
          item.yritys === this.searchcompany2 || "" === this.searchcompany2
      );
    },
    uniqueNames2: function() {
      let filtered_array = [];
      for (let i = 0; i < this.mittauspaikat.length; i++) {
        if (filtered_array.indexOf(this.mittauspaikat[i].yritys) === -1) {
          filtered_array.push(this.mittauspaikat[i].yritys);
        }
      }
      return filtered_array;
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    }
  },
  methods: {
    ...mapMutations([
      "DELETE_APPLICATION",
      "DELETE_APPLICATION2",
      "DELETE_CHOSEN",
      "DELETE_CHOSEN2",
      "ACCEPT_CHOSEN",
      "SAVE2",
      "SAVE3"
    ]),
    hideicons() {
      let x = document.getElementsByClassName("menubutton");
      for (let i = 0; i < x.length; i++) {
        x[i].style.display = "none";
      }
      x = document.getElementsByClassName("menubutton2");
      for (let i = 0; i < x.length; i++) {
        x[i].style.display = "none";
      }
      x = document.getElementsByClassName("v-input--checkbox");
      for (let i = 0; i < x.length; i++) {
        x[i].style.display = "none";
      }
      x = document.getElementsByClassName(
        "v-datatable__actions__range-controls"
      );
      for (let i = 0; i < x.length; i++) {
        x[i].style.display = "none";
      }
      document.getElementById("delete/acceptdiv").style.display = "none";
      document.getElementById("search").style.display = "none";
    },
    revealicons() {
      let x = document.getElementsByClassName("menubutton");
      for (let i = 0; i < x.length; i++) {
        x[i].style.display = "block";
      }
      x = document.getElementsByClassName("menubutton2");
      for (let i = 0; i < x.length; i++) {
        x[i].style.display = "block";
      }
      x = document.getElementsByClassName("v-input--checkbox");
      for (let i = 0; i < x.length; i++) {
        x[i].style.display = "block";
      }
      x = document.getElementsByClassName(
        "v-datatable__actions__range-controls"
      );
      for (let i = 0; i < x.length; i++) {
        x[i].style.display = "block";
      }
      document.getElementById("delete/acceptdiv").style.display = "block";
      document.getElementById("search").style.display = "block";
    },
    addlistener() {
      let savebutton = document.getElementById("tallennabutton");
      savebutton.addEventListener("click", this.save);
    },
    addlistener2() {
      let savebutton = document.getElementById("tallennabutton");
      savebutton.addEventListener("click", this.save2);
    },
    watchitem(item) {
      this.rownumber = item;
      this.hideicons();
      console.log(item);

      this.editedIndex = this.mittauspistehakemukset.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog2 = true;
    },
    watchitem2(item) {
      this.rownumber = item;
      this.hideicons();
      this.editedIndex = this.mittauspaikat.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog3 = true;
    },

    editItem(item) {
      this.addlistener();
      this.editedIndex = this.mittauspistehakemukset.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    editItem2(item) {
      this.hideicons();
      this.addlistener();
      this.editedIndex = this.mittauspistehakemukset.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    editItem3(item) {
      this.addlistener2();
      this.editedIndex = this.mittauspaikat.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    editItem4(item) {
      this.hideicons();
      this.addlistener2();
      this.editedIndex = this.mittauspaikat.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    deleteItem(item) {
      const index = this.mittauspistehakemukset.indexOf(item);
      this.DELETE_APPLICATION(index);
      this.revealicons();
      this.dialog2 = false;
    },
    deleteItem2(item) {
      const index = this.mittauspaikat.indexOf(item);
      this.DELETE_APPLICATION2(index);
      this.revealicons();
      this.dialog3 = false;
    },
    deletechosen() {
      if (
        confirm("Haluatko varmasti poistaa kyseiset mittauspistehakemukset?")
      ) {
        let selectedall = { key1: this.selected.length, key2: this.selected };
        this.DELETE_CHOSEN(selectedall);
      }
    },
    deletechosen2() {
      if (confirm("Haluatko varmasti poistaa kyseiset mittauspaikat?")) {
        let selectedall = { key1: this.selected2.length, key2: this.selected2 };
        this.DELETE_CHOSEN2(selectedall);
      }
    },
    acceptchosen() {
      if (
        confirm("Haluatko varmasti hyväksyä kyseiset mittauspistehakemukset?")
      ) {
        let selectedall = { key1: this.selected.length, key2: this.selected };
        this.ACCEPT_CHOSEN(selectedall);
      }
    },
    close() {
      this.revealicons();
      let savebutton = document.getElementById("tallennabutton");
      savebutton.removeEventListener("click", this.save);
      savebutton.removeEventListener("click", this.save2);

      this.dialog = false;
      this.dialog2 = false;
      this.dialog3 = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 20);
    },
    save() {
      if (this.editedIndex > -1) {
        Object.assign(
          this.mittauspistehakemukset[this.editedIndex],
          this.editedItem
        );
      } else {
        this.SAVE2(this.editedItem);
      }
      this.close();
    },
    save2() {
      if (this.editedIndex > -1) {
        Object.assign(this.mittauspaikat[this.editedIndex], this.editedItem);
      } else {
        this.SAVE3(this.editedItem);
      }
      this.close();
    }
  }
};
</script>

<style scoped>
.v-alert {
  color: #575756;
}
.header_hidden {
  display: none;
}
.toolbar {
  height: 13px;
}
.header1 {
  font-size: 7vw;
}
@media screen and (min-width: 480px) {
  .header1 {
    font-size: 35px;
  }
}
</style>
