  export default {
    state: { ...state },
    getters: {
      getApplications: state => state.mittauspistehakemukset,
      getPlaces: state => state.mittauspaikat
    },
    mutations: {
      DELETE_APPLICATION(state, index) {
        confirm('Haluatko varmasti poistaa tämän hakemuksen?') && state.mittauspistehakemukset.splice(index, 1)
      },
      DELETE_CHOSEN(state, selectedall){
        for(let i = 0; i <selectedall.key1; i++){
          const index = state.mittauspistehakemukset.indexOf(selectedall.key2[i]);
          state.mittauspistehakemukset.splice(index, 1);
        }
      },
      ACCEPT_CHOSEN(state, selectedall){
        for(let i = 0; i <selectedall.key1; i++){
          state.mittauspaikat.push(selectedall.key2[i]);
          const index = state.mittauspistehakemukset.indexOf(selectedall.key2[i]);
          state.mittauspistehakemukset.splice(index, 1);
        }
      },
      SAVE2(state, editedItem) {
        state.mittauspistehakemukset.push(editedItem)
      },
      DELETE_APPLICATION2(state, index) {
        confirm('Haluatko varmasti poistaa tämän mittauspisteen?') && state.mittauspaikat.splice(index, 1)
      },
      DELETE_CHOSEN2(state, selectedall){
        for(let i = 0; i <selectedall.key1; i++){
          const index = state.mittauspaikat.indexOf(selectedall.key2[i]);
          state.mittauspaikat.splice(index, 1);
        }
      },
      SAVE3(state, editedItem) {
        state.mittauspaikat.push(editedItem)
      }
    }
  }