<template>
  <div class="AddMeasurement">
    <b-button v-b-modal.lisaaModal>Lisää Mittaus</b-button>
    <b-modal ref="lisaaModal" id="lisaaModal" title="Lisää Mittaus" hide-footer>
      <b-form @submit="tallenna">
        <formGen
          ref="add"
          :schema="schema"
          v-model="formData"
          id="lisaaModal"
          title="Lisää Mittaus"
        />
        <b-button type="submit" @click="tallenna">Tallenna</b-button>
        <b-button type="reset" @click="peruuta">peruuta</b-button>
      </b-form>
    </b-modal>
    <p>{{ daatta }}</p>
  </div>
</template>

<script>
import formGen from "../components/AddFormGenerator.vue";
export default {
  name: "AddMeasurement",
  components: {
    formGen
  },
  data() {
    return {
      formData: {},
      daatta: {},
      schema: [
        //tähän muuttujat.JSON, josta schema
        {
          fieldType: "selectList",
          name: "tyyppi",
          label: "Mittaustyyppi",
          id: "mittaustyyppi",
          options: [
            "Lämpötila (°C)",
            "pH (0-14)",
            "Redox (mV)",
            "Sähkönjohtavuus (μS/cm)",
            "Happi (mg/l)",
            "Sameus (NTU)",
            "Suolaisuus (0 to 70 ppt)",
            "UV abs",
            "Escherichia coli (pmy/100 ml)",
            "Heterotrofinen pesäkelukumäärä (R2A-menetelmä)",
            "Sulfiittia pelkistävien klostridien itiöt",
            "Somaattiset kolifaagit",
            "Suolistoperäiset enterokokit (pmy/100 ml)",
            "F-spesifiset kolifaagit",
            "TOC (mgC/l)",
            "Kokonais fosfori (mg/l)",
            "Fe (ug/l)",
            "Mn (ug/l)"
          ]
        },
        {
          fieldType: "numberInput",
          name: "maara",
          id: "maara",
          label: "Määrä"
        },
        {
          fieldType: "selectList",
          name: "paikka",
          id: "paikka",
          label: "Mittauspaikka",
          options: ["Kuopio 1", "Kuopio 2"]
        },
        {
          fieldType: "dateInput",
          name: "paivays",
          id: "paivays",
          label: "Mittauspäivä"
        },
        {
          fieldType: "timeInput",
          name: "aika",
          id: "aika",
          label: "Mittausaika"
        },
        {
          fieldType: "numberInput",
          name: "testi",
          id: "testi",
          label: "numerotesti"
        },
        {
          fieldType: "textareaInput",
          name: "lisatietoja",
          id: "lisatietoja",
          label: "Lisätietoja"
        }
      ]
    };
  },
  methods: {
    closeModal() {
      this.$refs.lisaaModal.hide();
      Object.keys(this.formData).forEach(key => {
        this.formData[key] = "";
      });
    },
    tallenna(evt) {
      evt.preventDefault();
      this.daatta = Object.assign({}, this.formData);
      this.closeModal();
    },
    peruuta(evt) {
      evt.preventDefault();

      this.closeModal();
    }
  }
};
</script>

<style scoped>
.Add {
  color: blue;
}
</style>
