<template>
  <div>
    <label>{{label}}</label>
    <b-form-textarea :name="name" :value="value" @input="emit" maxlenght="500" rows="3"></b-form-textarea>
  </div>
</template>

<script>
export default {
  name: "textArea",
  props: ["name", "value", "label"],
  methods: {
    emit(value) {
      this.$emit("input", value);
    }
  }
};
</script>

